import React, { Component } from 'react';

import './App.css';
import {Link} from 'react-router';
import Sidenav from "./components/Sidenav";


class App extends Component {
     constructor(props) {
        super(props);
        this.state = {
            navbarStyle: {},
            marginY: 0,
            destination: 0,
            speed: 5,
            scroller: null
        };
        this.handleScroll = this.handleScroll.bind(this);
        this.initScroll = this.initScroll.bind(this);
    }

    componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
    }
    handleScroll(event) {
      const navbarStyle = {
          backgroundColor: "white",
          color: "black"
      };
      if (document.querySelector(".App-navbar")) {
          let scrollTop =
              window.pageYOffset 
              
          if (scrollTop >=10) {
              this.setState({ navbarStyle: navbarStyle });
          } else {
              this.setState({ navbarStyle: {} });
          }
      }
    }
        initScroll(elementId) {
        if (document.getElementById(elementId)) {
            this.setState(
                {
                    destination: document.getElementById(elementId).offsetTop
                },
                () => window.scroll(0, this.state.destination)
            );
        }
    }
  render() {
    return (
      <div className="App">
         <Sidenav />
        <div className="App-navbar" style={this.state.navbarStyle}>
         
          {/* <h5>Hi, 我是 <span className="App-name">Jun Xiao</span></h5> */}
          <div className="row">
            <Link to="/">Home</Link>
            <Link  to ="/project">Web Project</Link>
            <Link  to ="/research">PHD Research</Link>
            <Link  to ="/contact">Contact</Link>
            {/* <Link to ="/about">about</Link> */}
          </div>
        </div>
           
        {this.props.children}
        <div className="footer">
            © junxiao.me 
        </div>
      </div>
    );
  }
}

export default App;



