import React, { Component } from "react";
import PublicationPanel from './PublicationPanel';



export default class Publication extends Component {
    constructor(props) {
        super(props);
        this.state = {
            publication: require("./publication.json").publication,
          
        };
    }
    render() {
        return (
            <div className="research-panel">
            {this.state.publication.map((item, i) => (
                <PublicationPanel key={item.issue} item={item} index={i}/>
            ))}
            </div>
            
                
        );
    }
}



