import React from 'react';
import me from '../img/me.png'

export default () => {
  return (
    <div className="home">
      <div className="home-content">
        <p>Hello, 我是肖俊，欢迎来到我的主页。</p>

        <p>两年前，我从中山大学博士毕业后，去美国做博士后研究员。在那里，我对互联网产生了极大的兴趣，从美国归来后，我就开始自学相关互联网技术。</p>
      
        <p>学习的资料来源于Udemy， Udacity， Lynda， 慕课网等在线资源, 自学一年期间，结合我的科研经验，我做了edsolver.com以及 lalsci.com, 这两个网站主要是为了解决我在读博士期间遇到的问题，一个是晶体衍射的标定问题，另一个是我们领域文献查找的准确性和文献分类的问题。</p>

        <p>我是在线教育的重度用户。使用过多数知名的在线教育平台，而且付费购买了相关课程，或者成为它们的会员。对现有的在线教育产品有自己的理解。</p>
        
        <p>热爱教育，热爱互联网。</p>
      </div>
      
      <div className="div-App-me">
        <img className="App-me" src={me} alt="me"/>
      </div>
          
    </div>
  )
}


